import uniPopup from '@/components/uni-popup/uni-popup.vue'
import { api } from '@/config/common.js'
import { printSalesOrder } from '@/api/print.js'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			title: '打印单据',
			reqData: {
				model: '',
				isPrint: 1
			},
			returnPage: ''
		};
	},
	onLoad(options) {
		this.reqData.model = options.orderId
		this.returnPage = options.returnPage
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		mainHeight() {
			return this.$mainHeight
		}
	},
	methods: {
		handlePrint() {
			this.$refs.loading.open()
			printSalesOrder(api.Print, this.reqData).then(res => {
				this.$refs.loading.close()
				if (res.status == 200) {
					switch(res.data.returnCode) {
						case '0000':
							uni.showToast({
								icon: 'success',
								title: '打印单据成功'
							})
							setTimeout(()=>{
								uni.navigateTo({
									url: this.returnPage
								})
							},500)
							break
						case '101':
							this.$refs.popup.open()
							break
						default: 
							uni.showToast({
								icon: 'none',
								title: res.data.returnMessage
							})
							setTimeout(()=>{
								uni.navigateTo({
									url: this.returnPage
								})
							},500)
					}
				} else {
					uni.showToast({
						icon: 'none',
						title: res.data.returnMessage
					})
				} 
			}).catch(error => {
				this.$refs.loading.close()
				uni.showToast({
					icon: 'none',
					title: error
				})
			})
		},
		handleNext() {
			uni.navigateTo({
				url: this.returnPage
			})
		},
		handleCancelSet() {
			this.$refs.popup.close()
			uni.navigateTo({
				url: this.returnPage
			})
		},
		handleGoSet() {
			uni.navigateTo({
				url: '/pages/my/print/print'
			})
		}
	}
}
